<template>
  <div id="home">
    <nav-bar class="home-nav">
      <!--  -->
      <div slot="center"></div>
    </nav-bar>
    <home-swiper :banners="banners"></home-swiper><!--  -->
    <home-recommend :recommends="recommends"></home-recommend><!--  -->
    <feature-view /><!--  -->
    <tab-control class="tabcontrol" :titles="['','','']" @tabClick="tabClick" />
    <goods-list :goods="this.goods[currentType].list" />
  </div>
</template>

<script>
  //
  import HomeSwiper from './homeComps/homeSwiper.vue'
  import HomeRecommend from './homeComps/homeRecommend.vue'
  import FeatureView from './homeComps/FeatureView.vue'

  //
  import NavBar from 'components/common/navbar/NavBar.vue'
  import TabControl from '../../components/content/tabControl/TabControl.vue'
  import GoodsList from '../../components/content/goodsList/GoodsList.vue'


  //
  import {
    getHomeMultidata,
    getGoodsData
  } from 'network/home'

  export default {
    components: {
      NavBar,
      HomeSwiper,
      HomeRecommend,
      FeatureView,
      TabControl,
      GoodsList,
    },
    data() {
      return {
        banners: [],
        recommends: [],
        goods: {
          'pop': {
            page: 0,
            list: []
          },
          'new': {
            page: 0,
            list: []
          },
          'sell': {
            page: 0,
            list: []
          }
        },
        currentType: 'pop',
      }
    },
    created() {
      //
      this.getHomeMultidata()
      //
      this.getGoodsData('pop')
      this.getGoodsData('new')
      this.getGoodsData('sell')
    },
    methods: {
      //
      tabClick(index) {
        switch (index) {
          case 0:
            this.currentType = 'pop'
            break
          case 1:
            this.currentType = 'new'
            break
          case 2:
            this.currentType = 'sell'
            break
        }
      },

      //
      getHomeMultidata() {
        getHomeMultidata().then(res => {
          // console.log(res);
          this.banners = res.data.data.banner.list
          this.recommends = res.data.data.recommend.list
        })
      },
      getGoodsData(type) {
        const page = this.goods[type].page + 1
        getGoodsData(type, page).then(res => {
          // console.log(res.data.data);
          // for (let list of res.data.data.list) {  //
          //   this.goods[type].list.push(list)
          // }
          this.goods[type].list.push(...res.data.data.list) //Home.vue
          this.goods[type].page += 1
        })
      }
    }
  }
</script>

<style>
  .home-nav {
    background-color: var(--color-tint);
    color: white;
  }

  img {
    width: 100%;
  }

  #home {
    padding-top: 44px;
    /**/
  }

  .tabcontrol {
    position: sticky;
    /**/
    top: 44px
      /**/
  }
</style>