<template>
  <div class="tab-control">
    <div v-for="(item, index) in titles" 
         :key="index" 
         class="tab-control-item" 
         :class='{active:index === currentIndex}'
         @click="itemClick(index)">
      <span>{{item}}</span>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      titles: {
        type: Array,
        defaule: []
      }
    },
    data() {
      return {
        currentIndex: 0
      }
    },
    methods:{
    itemClick(index){
      this.currentIndex = index
      this.$emit('tabClick',index)
    }
    },
  }
</script>

<style>
  .tab-control {
    display: flex;
    text-align: center;
    font-size: 14px;
    background-color: #fff;
    z-index: 99;
  }

  .tab-control-item span{
    padding: 3px;
  }

  .tab-control-item {
    flex: 1;
    height: 40px;
    line-height: 40px;
  }

  .active{
    color: var(--color-high-text);
  }

  .active span{
    border-bottom: 2px solid var(--color-high-text);
  }
</style>